@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  overflow: hidden;
}
h1,h2,h3,h4,h5,h6{
  font-family: 'Inter', sans-serif;
}
.main-wrapper {
  text-align: center;
}

a {
  text-decoration: none;
}

.headerNav .middle {
  text-align: center;
}
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-background-clip: text;
    transition: background-color 5000s ease-in-out 0s;
    box-shadow: inset 0 0 0px 0px #23232329;
}
ul {
  margin: 0;
  padding: 0;
}

.mr {
  margin-right: 12px;
}
table thead tr th {
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0.12px;
  padding: 11px 16px !important;
  text-align: left;
  vertical-align: middle;
  color: #475467 !important;
}
table tbody tr td {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0.14px;
  vertical-align: middle;
  padding: 4.8px 15px !important;
}
table tbody tr:nth-child(odd) td {
  background: #F9FAFB;
}
table tbody tr:hover td {
  background: #F9F5FF;
}
.filterSearch:focus-visible,.filterSearch input {
  border: 0 !important;
  outline: 0;
}
:focus-visible, input {
  border: 0 ;
  outline: 0;
}
.ContactModelIn textarea::placeholder {
  color: #98A2B3;
  opacity: 1; 
}

.ContactModelIn textarea::-ms-input-placeholder { 
  color: #98A2B3;

}
.modal-backdrop {
  background-color: #1018281a !important;
}
.flexEndbox {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.innerTableStyle tr td {
  border: 0;
}
table.innerTableStyle tr, table.innerTableStyle {
  text-align: center;
  width: 100%;
}
.flexcenterbox {
  display: flex;  
  align-items: center;
  justify-content: center;
}
.tooltipDefaultStyle,.tooltipDefaultStyle:hover,.tooltipDefaultStyle:focus{
  background: initial;
  border: initial;
  color: initial;
  text-transform: inherit;
  font-size: inherit;
  color: var(--Gray-600, #475467);
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px; 
  letter-spacing: 0.25px;
}
.iconOutStyle {
  width: 48px;
  background: #ECFDF3;
  height: 48px;
  padding: 4px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-right: 11px;
}

.iconinStyle {
  width: 32px;
  background: #DCFAE6;
  height: 32px;
  /* padding: 4px; */
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;  
  cursor: pointer;
}
.iconinnerStyle {
  border-radius: 16px;
  border: 1px solid #ECFDF3;
  background: #DCFAE6;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px;
}
.modelStyle .modal-header span, .popoverHead span {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.modelStyle .modal-header span,.popoverHead span {
  color: #344054;
  font-size: 22px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px;
  letter-spacing: -0.33px;
}
.savebox,.popoverbottom .savebox {
  border-radius: 4px;
  background: #9E77ED;
  padding: 10px 18px;
  color: #fff;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  border: 1px solid #9E77ED;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.savebox:hover, .popoverbottom .savebox:hover {
  border-radius: 4px;
  background: #9E77ED;
  padding: 10px 18px;
  color: #fff;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  border: 1px solid #9E77ED;
  opacity: .98;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.closebox, .popoverbottom .closebox {
  border-radius: 4px;
  background: #fff;
  padding: 10px 18px;
  color: #344054;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  border: 1px solid #D0D5DD;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;

}
.ContactModelIn .text-danger {
  color: var(--Error-500, #F04438);
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
}
.btn-check:checked+.btn, .btn.active, .btn.show, .btn:first-child:active, :not(.btn-check)+.btn:active {
  border-color: transparent;
}
.closebox:hover, .popoverbottom .closebox:hover {
  border-radius: 4px;
  background: #9E77ED;
  padding: 10px 18px;
  color: #fff;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  border: 1px solid #9E77ED;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  /* width: 288px; */
}
.modelStyle .modal-footer, .popoverbottom {
  justify-content: flex-end;
  display: flex;
}

.modelStyle .modal-header,.popoverHead {
  border-bottom: 1px solid #EAECF0;
}
.modelStyle .modal-footer, .popoverbottom {
  border-top: 1px solid #EAECF0;
  padding: 12px 24px 16px !important;
  margin-top: 13px;
}
.modelStyle textarea {
  border-radius: 4px;
  border: 1px solid #D0D5DD;
  background: #fff;
}
.modelStyle label {
  color: #667085;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}
tr.nodataTableRow {
  height: calc(100vh - 220px);
}
tr.nodataTableRow td {
  border: 0;
  background: transparent !important;
}
.modelStyle .form-floating {
  position: relative;
  margin: 24px 0;
}
.modelStyle .modal-dialog {
  max-width: 589px; 
}
.modelStyle .modal-content {
 border: 0;
 border-radius: 4px;
 background: #fff;
}
.QuoteLost .iconinnerStyle {
  border-radius: 16px;
  border: 1px solid #fef3f2;
  background: #FEE4E2;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6px;
}
.flatpickr-current-month input.cur-year {
  background: #fff !important; 
}
.numInputWrapper span {
  border: 0px solid rgba(72,72,72,0.15)!important; 
  right: -8px!important; 
}
.flatpickr-current-month .numInputWrapper span.arrowUp:after {
  border-bottom-color: #000!important; 
}
.flatpickr-current-month .numInputWrapper span.arrowDown:after {
  border-top-color: #000!important; 
}
.numInputWrapper span:hover {
  background: rgb(0 0 0 / 0%)!important; 
}
.QuoteLost .iconOutStyle {
  width: 48px;
  height: 48px;
  padding: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-right: 11px;
  border-radius: 28px;
  border: 8px solid var(--Error-50, #FEF3F2);
  background: var(--Error-100, #FEE4E2);
}
.tableTopBar .tabActive {
  border-radius: 4px;
  border: 1px solid #7F56D9;
  background: #7F56D9;
  font-size: 14px !important;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  margin-left: 9px;
  padding: 4px 13px;
  color: #fff !important;
  min-width: 59px;
  height: 28px;
  display: flex;
  align-items: center;
}
table.table {
  margin-bottom: 0;
}
.popoverHead,.popoverbottom {
  padding: 15px;
}
.linkByttonStyle, .linkByttonStyle:hover {
  color: #9E77ED;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  text-decoration: none;
  display: inline-block;
  padding: 0;
}
.clientAddress .linkByttonStyle{
  color: #9E77ED;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  text-decoration: none;
  display: inline-block;
  padding: 0;
  border-radius: 8px;
  border: 1px solid var(--Primary-200, #E9D7FE);
  background: var(--Primary-50, #F9F5FF);
  width: 40px;
  height: 40px;
  text-align: center;
}
.clientAddress .linkByttonStyle svg{
  margin-left: 0;
}
table tbody tr .linkByttonStyle, .linkByttonStyle{
  opacity: 0;
}
table tbody tr:hover .linkByttonStyle, .linkByttonStyle {
  opacity: 1;
}

.linkByttonStyle svg {
  margin-left: 4px;
}
.leftStyle span {
  padding-right: 10px;
  display: inline-block;
  min-width: 125px;
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
}
button {
  cursor: pointer;
}

.boxLogin input:focus {
  border: 0;
  outline: none;
}

.error-message {
  color: var(--color6);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0.14px;
  margin-top: 3px;
  margin-bottom: 0;
}

.inputInfo img.ExclamationCircle {
  margin-right: 0;
  opacity: 0;
}

.error-border img.ExclamationCircle {
  opacity: 1;
}

.inputInfo.error-border {
  border: 1px solid #FDA29B !important;
}
.inputInfo.successBorder {
  border: 1px solid #D6BBFB !important;
}
/* Table */
.tableStyle {
  border-collapse: collapse;
  width: 100%;
}

.tableStyle td, .tableStyle th {
  border: 1px solid var(--color10);
  background: var(--color2);
  padding: 8px;
  padding: 4px 16px;
  vertical-align: middle;
  text-align: left;
  white-space: nowrap;
}

.tableStyle tr:nth-child(even) {
  background: var(--color9);
}

.tableStyle tr:hover {
  background-color: #ddd;
}

.tableStyle thead tr th {
  color: var(--colorG3);
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0.12px;
  padding: 12px 12px 12px 16px;
}

.alighCenter {
  text-align: center;
}

#noneStyle-btn p {
  margin: 0;
  padding: 0;
}
#noneStyle-btn {
  color: var(--colorG4);
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0.12px;
  background: transparent;
  box-shadow: none !important;
  display: block;
  padding: 0;
  margin: 0 auto;
}

ul.listStyle li {
  list-style: none;
  display: inline-block;
  padding-right: 12px;
}

ul.listStyle li:last-child {
  padding-right: 0px;
}

.paddingLR{
  padding: 0 19px;
}



/* Home Page Style */
.topHeadingHome h1 {
  color: var(--Gray-900, #101828);
  font-family: Inter;
  font-size: 26px;
  font-style: normal;
  font-weight: 600;
  line-height: 32px;
  letter-spacing: -0.39px;
}
.HomePageWrap .grid-item {
  border-radius: var(--radius-xs, 4px);
  border: 1px solid var(--Gray-200, #EAECF0);
  background: var(--Base-White, #FFF);
  padding-top: 16px;

}
.HomePageWrap .grid-item label input {
  display: none;
}
.HomePageWrap .grid-item label {
  text-align: left;
  padding: 0px 24px 16px;
}
.boxCheckNo span {
  border-radius: 28px;
  border: 4px solid var(--Gray-50, #F9FAFB);
  background: var(--Gray-100, #F2F4F7);
  mix-blend-mode: multiply;
  width: 32px;
  height: 32px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--Gray-700, #344054);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  
}
.boxCheckNo {
  margin-bottom: 18px;
}
.HomePageWrap .grid-item.active .boxCheckNo .ckeckIcon {
  display: flex;
  border-radius: 28px;
  border: 4px solid var(--Primary-50, #F9F5FF);
  background: var(--Primary-100, #F4EBFF);
  mix-blend-mode: multiply;
  width: 32px;
  height: 32px;
  justify-content: center;
  align-items: center;
}
.HomePageWrap .grid-item.active .boxCheckNo .ckeckIcon {
  display: flex;
}
.HomePageWrap .grid-item .boxCheckNo span {
  display: block;
}
.HomePageWrap .grid-item.active .boxCheckNo span {
  display: none;
}
.HomePageWrap .grid-item .boxCheckNo .ckeckIcon{
  display: none;
}
.boxItemBut {
  border-top: 1px solid #EAECF0;
  padding: 16px 24px;
}
.boxItemBut button {
  color: #6941C6;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  text-decoration: none;
  border-radius: 4px;
  border: 1px solid var(--Primary-200, #E9D7FE);
  background: var(--Primary-50, #F9F5FF);
  padding: 8px 14px;
}
.HomePageWrap .grid-item.active .boxItemBut button {
  border-radius: 4px;
  border: 1px solid var(--Primary-600, #7F56D9);
  background: var(--Primary-600, #7F56D9);
  color: #FFF;
}
.HomePageWrap .boxItem h2 {
  color: var(--Gray-900, #101828);
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
}
.HomePageWrap .boxItem p {
  overflow: hidden;
  color: var(--Gray-600, #475467);
  text-overflow: ellipsis;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
}
.HomePageWrap .grid-item:hover button {
  border-radius: 4px;
  border: 1px solid var(--Primary-600, #7F56D9);
  background: var(--Primary-600, #7F56D9);
  color: #FFF;
}
.HomePageWrap .grid-item input[type="radio"] {
  width: 100%;
  height: 100%;
}
.HomePageWrap .grid-item.active {
  border-radius: var(--radius-xs, 4px);
  border: 1px solid var(--Primary-200, #E9D7FE);
  background: var(--Primary-50, #F9F5FF);
}
.bigBoxHome {
  border-radius: var(--radius-xl, 12px);
  border: 1px solid var(--Gray-100, #F2F4F7);
  background: #FFF;
  padding: 24px;
  position: relative;
}


.TooltipOverlay [data-popper-placement^=top] .tooltip-arrow::before, .TooltipOverlay  .tooltip-arrow::before {
  top: -1px;
  border-width: var(--bs-tooltip-arrow-height) calc(var(--bs-tooltip-arrow-width) * .5) 0;
  border-top-color: #fff;
}
.TooltipWrapper button:hover, .TooltipWrapper button:focus, .TooltipWrapper button:active, .TooltipWrapper button:active:focus-visible {
  background: transparent;
  border: 0;
  box-shadow: none !important;
}
.TooltipOverlay .tooltip-inner {
  background: #fff;
  box-shadow: 0px 12px 16px -4px rgba(16, 24, 40, 0.08), 0px 4px 6px -2px rgba(16, 24, 40, 0.03);
  border: 6px;
  color: #000;
  text-align: left;
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
  padding: 10px;
}
.TooltipWrapper button {
  background: transparent;
  border: 0;
  padding: 0;
  position: absolute;
  right: 15px;
  top: 4px;
}
.bigBoxHome h3 {
  color: var(--Gray-600, #475467);
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.154px;
}
.bigBoxHome h4 {
  color: var(--Gray-700, #344054);
  font-family: Inter;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.39px;
  margin-bottom: 0;
}
