.goodHeading {
  /* position: sticky; */
  /* top: 0; */
  background: #f7f9fc;
  padding: 13px 0 13px;
  z-index: 9;
}
.imageBoxHome:hover {
  -webkit-filter: sepia(1);
  filter: sepia(1);
}
.HomePageWrap {
  background: var(--BG, #F7F9FC);
  overflow-y: auto;
  height: calc(100vh - 149px);
  padding-bottom: 16px;
}
.homeBottom {
  background: var(--BG, #F7F9FC);
  padding-bottom: 28px;
}
.HomePageWrap::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  padding: 5px;
}
.homeBottom ul li {
  display: inline-block;
  margin: 0 8px;
}
.HomePageWrap::-webkit-scrollbar-track {
  box-shadow: inset 0 0 0px #fff; 
  border-radius: 10px;
}
.HomePageWrap::-webkit-scrollbar-thumb {
