.HomePageWrap::-webkit-scrollbar-thumb:hover {
  background: #EAECF0; 
}




.countNoBox span {
  font-size: 56px;
  font-style: normal;
  font-weight: 600;
  line-height: 34px;
  letter-spacing: -1.4px;
  padding: var(--spacing-3xl, 24px) var(--spacing-4xl, 32px);
}
.HomePageWrap {
  background: var(--BG, #F7F9FC);
}
.imageBoxHome {
  height: 196px;
  padding: var(--spacing-lg, 12px) var(--spacing-xl, 16px);
  align-items: center;
  gap: var(--spacing-xl, 16px);
  flex: 1 0;
  background-position: center center;
  border-radius: var(--radius-xs, 4px);
  width: 154px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  position: relative;
  text-align: center;
}
.imageBoxHome h3 {
  color: #FFF;
  text-align: center;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.08px;
  position: absolute;
  top: 12px;
  left: 0;
  right: 0;
  margin: 0 auto;
}
.imageBoxHome:before {
  position: absolute;
  content: "";
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.40) -48.32%, rgba(0, 0, 0, 0.00) 103.16%), linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, rgba(0, 0, 0, 0.40) 100%);
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: var(--radius-xs, 4px);
}
.imageBoxHome .textOverly {
  z-index: 999;
}
.imageBoxHome button,.imageBoxHome button:hover {
  border-radius: 28px;
  border: 1px solid #FFF;
  display: flex;
  padding: 4px 14px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  text-decoration: none;
  color: #FFF;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  margin: 0 auto;
}
.bigBoxHome .pluslgIcon {
  position: absolute;
  right: -7px;
