}
.navStepsticky {
    padding: 25px 38px;
}
.navStepsticky .formgroupWrap1 {
    width: 720px;
    margin: 0 auto;
}
.newQuotePage .dFlex {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.newQuotePage .newQuoteBack {
    position: absolute;
    left: 42px;
}
.newQuoteContent h3 {
    color: var(--Gray-700, #344054);
    font-family: Inter;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    letter-spacing: -0.33px;
    text-align: left;
    border-bottom: 1px solid #EAECF0;
    padding-bottom: 29px;
}
.formgroupWrap ul {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 720px;
}
.formgroupWrap ul li a,.formgroupWrap1 ul li a {
    border-radius: var(--radius-xs, 4px);
    border: 1px solid #EAECF0;
    background: #FFF;
    padding: 12px 16px;
    color: #344054;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
}
.formgroupWrap ul li {
    width: 48%;
    text-align: left;
}
.formgroupWrap ul li a span {
    border-radius: var(--radius-full, 9999px);
    background: var(--Primary-50, #F9F5FF);
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 8px;
    padding: 0;
}
.formgroupWrap1 ul li a span {
    border-radius: 4px;
    background: linear-gradient(180deg, #F9FAFB 0%, #EDF0F3 100%);
    border: 0.75px solid #d8d8d8;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 8px;
    padding: 0;
}
.newQuotePage .newQuoteBack button {
    color: var(--Gray-800, #1D2939);
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px;
    border-radius: 4px;
    border: 1px solid var(--Gray-300, #D0D5DD);
    background: var(--Base-White, #FFF);
    padding: 10px 16px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.navStepClient ul {
    display: flex;
    justify-content: center;
    align-items: center;
}
.navStepClient ul li span {
    border-radius: 4px;
    border: 1px solid var(--Gray-200, #EAECF0);
    background: var(--Base-White, #FFF);
    display: flex;
    width: 40px;
    height: 40px;
    padding: 10px;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    position: relative;
    z-index: 999;
}
.navStepClient ul li:first-child:before {
    position: absolute;
    background: #EAECF0;
    width: 100%;
    height: 2px;
    content: "";
    top: 22%;
    z-index: 1;
}
.navStepClient ul li:nth-child(2):before {
    position: absolute;
    background: #EAECF0;
    width: 100%;
    height: 2px;
    content: "";
    top: 22%;
    z-index: 1;
}
.navStepClient ul li {
    text-align: center;
    position: relative;
    padding: 0 34px;
}
.navStepClient ul li p {
    color: var(--Gray-700, #344054);
    text-align: center;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: 0.175px;
    margin-top: 12px;
}
.navStepClient .activeClientTab span svg {
    fill: #7F56D9;
}
.navStepClient .activeClientTab span {
    border-radius: 4px;
    border: 1px solid var(--Primary-300, #D6BBFB);
    background: var(--Primary-50, #F9F5FF);
}
