    border: 1px solid var(--Gray-200, #EAECF0);
    background: var(--Base-White, #FFF);
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
}

.topHeadStyle h2.Exclamation {
    display: flex;
    align-items: center;
    color: var(--Gray-600, #475467);
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    margin: 0;
}

.topHeadStyle h2.Exclamation strong {
    color: var(--Gray-700, #344054);
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 600 !important;
    line-height: 20px;
    margin-right: 4px;
}

.gridInfoWrap {
    display: flex;
}

.settings-BillingInfo .dflex {
    display: flex;
    align-items: self-start;
}

.content_wrapper1 .topHeadStyle {
    padding-bottom: 12px;
    margin-bottom: 27px;
    border-bottom: 1px solid #EAECF0;
    background: var(--Base-White, #FFF);
}

.gridInfoWrap ul li span {
    display: block;
    color: var(--Gray-500, #667085);
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0.14px;
}

.gridInfoWrap ul li strong {
    color: var(--Gray-600, #475467);
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
