    letter-spacing: 0.08px;
}

.gridInfoWrap {
    display: flex;
    width: 100%;
}

.gridInfoWrap ul {
    width: 33.33%;
}

.gridInfoWrap ul li {
    margin-bottom: 34px;
    padding-right: 19px;
}
.billingwrapper .head {
    border-bottom: 1px solid #EAECF0;
}
.billingwrapper .head h2 {
    color: var(--Gray-900, #101828);
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    margin-bottom: 14px;
}
.billingwrapper .boxWrap {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
.billingwrapper label {
    display: flex;
    justify-content: space-between;
}   
.billingwrapper .grid-item {
    margin-top: 25px;
    border-radius: var(--radius-xs, 4px);
    border: 1px solid #EAECF0;
    background: #FFF;
    padding: var(--spacing-xl, 16px);
}
.billingwrapper .boxItem h2 {
    color: #344054;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    
}
.billingwrapper .boxItem p {
    color: #475467;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    margin: 0;
}
.billingwrapper .boxItem {
    margin-left: 12px;
}
.billingwrapper button {
    color: #475467;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px;
    border: 0;
    background: transparent;
    padding: 0;
    margin-top: 8px;
}
.billingwrapper button.close {
    color: #F04438;
    margin-left: 12px;
}
.billingwrapper .boxCheckNo .ckeckIcon {
    border-radius: var(--radius-xs, 4px);
    border: 1px solid #D0D5DD;
    width: 16px;
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.billingwrapper label input[type="radio"] {
    width: 100%;
    height: 100%;
    cursor: pointer;
    display: none;
}
.billingwrapper .grid-item.active {
    border-radius: var(--radius-xs, 4px);
    border: 2px solid #7F56D9;
    background: #FFF;
}
.billingwrapper .grid-item.active .boxCheckNo .ckeckIcon {
    border-radius: var(--radius-xs, 4px);
    background: #7F56D9;
}
.billingwrapper .grid-item.active .boxCheckNo .ckeckIcon svg {
    fill: #ffffff;
}




.stepBoxStylePayment .formgroup .inputInfo {
    border-radius: var(--radius-xs, 4px);
    border: 1px solid var(--Border, #D0D5DD);
    background: var(--Base-White, #FFF);
    margin: 4px 0 22px;
    padding: 10px 8px 6px;
}
.stepBoxStylePayment .formgroup label {
    color: var(--Gray-600, #475467);
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0.14px;
}
.stepBoxStylePayment .formgroup .inputInfo input {
    color: var(--Gray-500, #667085);
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: -0.08px;
    width: 100%;
}
.modelHeadFlex {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.modelHeadFlex h2 {
    color: var(--Gray-700, #344054);
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    letter-spacing: -0.27px;
    margin: 0;
}
.modelHeaderBillig {
    padding: 16px 24px 12px 24px;
    border-bottom: 1px solid #EAECF0;
}
.stepBoxStylePayment {
    padding: 10px 23px 13px 23px;
    background: var(--Gray-25, #FCFCFD);
    display: grid;
    gap: 0 18px;
    grid-template-columns: 58% 39%;
}
.footerButton {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    border-top: 1px solid #EAECF0;
    padding: 12px 24px 16px !important;
}
.footerButton button.save {
    border-radius: 4px;
    background: #9E77ED;
    padding: 10px 18px;
    color: #fff;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    border: 1px solid #9E77ED;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Inter';
}
.footerButton button.Cancel {
    border-radius: 4px;
    background: #fff;
    padding: 10px 18px;
    color: #344054;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    border: 1px solid #D0D5DD;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 12px;
    font-family: 'Inter';
}